import React, {Component} from 'react';


class Blog extends Component {
    constructor(props){
        super(props);
        this.state={
            items:[],
            currentItem:{
                text:'',
                key:''
            }
        }
        this.handleInput = this.handleInput.bind(this);
        this.addItem = this.addItem.bind(this);
    }
    handleInput(e){
        this.setState({
            currentItem:{
                text: e.target.value,
                key:Date.now()
            }
        })
    }

    addItem(e){e.preventDefault();
        const newItem = this.state.currentItem;
        console.log(newItem);
        if(newItem.text!==""){
            const newItems = [...this.state.items, newItem];
            this.setState({
                items:newItems,
                currentItem:{
                    text:'',
                    key:''
                }
            })
        }

    }
    render() {

        return (
            <div>
                <h1>Hi there, I'm Stephanie Opala</h1>
                <p>I am a Junior Front-End Developer proficient in HTML5, CSS3, Bootstrap and JavaScript. I have a passion for technology and I am currently learning React.</p>
                <h2>My goals and aspirations</h2>
                <div className="TodoForm" onSubmit={this.addItem}>
                    <form id="to-do-form" >
                        <input type="text" placeholder="Enter text"
                        value={this.state.currentItem.text}
                        onChange={this.handleInput}  />
                        <button type="submit" >Add</button>
                    </form>
                    
                </div>
            </div>    
        )

    }
}
export default Blog;